.loading-icon{
    border: 4px solid #b8b7b7; /* Light grey */
    border-top: 4px solid var(--darkYellow); /* Blue */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 2s linear infinite;    
    margin: 0 auto;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }